body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
}
.background{
    max-width: 100%;
    position:fixed;
    z-index: -2;
}
.leftPanel{
  max-width: 20%;
}
.nikeLogo{
  margin-top: 5%;
  margin-left: 25%;
  max-height: 100px;
  max-width: 50%;
}
.header{
  margin-left: 25%;
  margin-top: 25%;
  font-size: 42px;
  font-weight: 900;
  margin-bottom: 15%;
}

.sideNavOption{
  margin-left: 25%;
  font-weight: 800;
  cursor: pointer;
}
.sideNav{
  margin-top: 20%;
}
.leftPanelShoe{
  margin-top: 20%;
  max-width:60%;
  margin-left: 5%;
  position: relative;
  transform: scaleX(-1);
}

.group {
  display: flex;
  align-content: center;
}
.groupCart {
  display: flex;
  align-content: center;
  justify-content: flex-end;
}
.miniPanel {
  margin-top: 22%;
  position: relative;
  left:15%
}
.dot{
  max-width: 7px;
  max-height: 7px;
  cursor: pointer;
}
.mainImage{
  transform: rotate(-40deg);
  position: relative;
  z-index: -1;
  left: 2%;
}
.info{
  margin-top: 2%;
  position: relative;
  right: 8%%;
}
.cart{
  font-weight: 900;
  margin-right: 5%;
  margin-top: 2%;
}
.cartIcon{
  max-width: 50px;
  max-height: 50px;
  text-align: right;
}
.shoeName{
  margin-top: 15%;
  font-weight: 900;
  font-size: 60px;
  background-image: linear-gradient(to right, #000, #bf0d0d 25%,  orange 75%);
  color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
}
.price{
  font-weight: 900;
  font-size: 42px;
}
.desc{
  font-size: 16px;
  line-height: 200%;
  max-width: 80%;
  color: dimgray;
}
.addToCart{
  background-color: black;
  color: white;
  text-align: center;
  width: 80%;
  height: 12%;
  font-size: 24px;
  font-weight: 900;
}
.arrowLeft{
  max-width: 200px;
  max-height: 50%;
  transform: rotate(-180deg);
}
.arrowRight{
  max-width: 200px;
  max-height: 50%;
}
.linksMenu{
  margin-top: 30%;
}
.link{
  margin-bottom: 80%;
  font-weight: 900;
  transform: rotate(-90deg);
}
